import http from '../../common/getList.js';
export default {
	data() {
		return {
			name: '',
			phone: '',
			year: '',
			school:'',
			banji:'',
			city:'',
			xianqu:'',
			quyu:'',
			video_url: '',
			video1: '',
			videoList: [
				{
					url: '../../static/image/zhanshi.mp4',
					name: '案例视频参考',
					id: '0001'
				}
			],
			currentPlay: null,
			isplay: false,
			img1:'',    //选择图片本地地址
			img2:'',
			img3:'',
			imgurl1:'',
			imgurl2:'',  //上传返回之后的地址
			imgurl3:''
		};
	},
	methods: {
		commit(e) {
			var that = this;

			// if (that. === '') {
			// 	uni.showModal({
			// 		title: '温馨提示',
			// 		content: '请等待视频上传成功！'
			// 	});
			// }
			var myreg = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1})|(17[0-9]{1}))+\d{8})$/;
			if (!myreg.test(that.phone)) {
				uni.showModal({
					title: '温馨提示',
					content: '手机号有误！'
				});
			} else {
				let url = 'api/home/index/submitPost1';
				let data = {
					name: that.name,
					mobile: that.phone,
					age: that.year,
					school:that.school,
					banji:that.banji,
					pic_url1:that.imgurl1,
					pic_url2:that.imgurl2,
					pic_url3:that.imgurl3
				};
				http.request(url, data).then(
					res => {
						if (res.code === 1) {
							uni.showToast({
								title: '提交成功'
							});
							that.name = '';
							that.phone = '';
							that.year = '';
							that.school='';
							that.banji='';
							that.img1 = '';
							that.img2 = '';
							that.img3 = '';
						} else {
							uni.showModal({
								title: '温馨提示',
								content: res.msg
							});
						}
					},
					error => {}
				);
			}
		},
		chooseVideo(index) {
			var that = this;
			uni.chooseImage({
				count: 1,
				sourceType: ['album', 'camera'],
				sizeType:['compressed'],
				success: function(res) {
					console.log(res);
					if(index===1){
						that.img1 = res.tempFilePaths[0];
						that.uploadImg(that.img1,1)
					}else if(index===2){
						that.img2 = res.tempFilePaths[0];
						that.uploadImg(that.img2,2)
					}else if(index===3){
						that.img3 = res.tempFilePaths[0];
						that.uploadImg(that.img3,3)
					}
				}
			});
		},
		uploadImg(url,index){
			console.log(index);
			var that=this
			var uploadTask = uni.uploadFile({
				url: 'http://dongao.agampai.com/api/home/index/upload', //仅为示例，非真实的接口地址
				filePath: url,
				name: 'file',
				files: url,
				success(res) {
					console.log(res);
					if (res.data.code === 0) {
						uni.showModal({
							title: '温馨提示',
							content: res.msg
						});
					}
					if (res.statusCode === 200) {
						uni.showToast({
							title: '图片选择成功'
						});
						if(index===1){
							that.imgurl1 = res.data;
						}else if(index===2){
							that.imgurl2 = res.data;
						}else if(index===3){
							that.imgurl3 = res.data;
							console.log(that.imgurl3);
						}
					} else {
						uni.showToast({
							title: '图片选择失败！',
							icon: none
						});
					}
				},
				fail: error => {}
			});
			uploadTask.onProgressUpdate(res => {
				uni.showLoading({
					title: '图片上传中' + res.progress + '%'
				});
				if (res.progress == 100) {
					uni.hideLoading();
				}
			});
		},
		playvideo(e) {
			var index = e.currentTarget.dataset.index;

			this.currentPlay = index;
		},
		playend(e) {
			this.currentPlay = null;
		},
		playenda(e) {
			// var url = this.video_url;
			// this.video_url = '';
			// this.video_url = url;
		}
	}
};
